const Base = require('../base.js');
//删除管文章
module.exports = class extends Base {
    async indexAction() {
        const model = this.model('spxq');
        let affectedRows = await model.where({
            id: this.ctx.post("id")
        }).delete();
        var jhde = {}
        if (affectedRows == 1) {
            jhde.code = 0
            jhde.msg = "删除商品成功"
        } else {
            jhde.code = -1
            jhde.msg = "删除商品失败"
        }
        return this.fail(jhde); //2输出json 推荐

    }

};
